import React from 'react'
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs'
import KN1 from './KN1'
import KNN2 from './KNN2'
import KN3 from './KN3'
import KN4 from './KN4'
import KN5 from './KN5'

const Tab = createMaterialTopTabNavigator();


export default function Kanji() {
  return (
    <Tab.Navigator screenOptions={{
      tabBarStyle: { elevation: 0, backgroundColor: '#F48A58', height: 80 },
      tabBarLabelStyle: { fontSize: 18, fontWeight: '500', marginTop: 20 }, tabBarIndicatorStyle: {
        backgroundColor: 'white', height: 50, width: 50, borderRadius: 15, marginBottom: 12, marginLeft: 10
      }, tabBarPressColor: '#F48A58', tabBarActiveTintColor: '#F48A58', tabBarInactiveTintColor: 'white'
    }}>
      <Tab.Screen name="N5" component={KN5} />
      <Tab.Screen name="N4" component={KN4} />
      <Tab.Screen name="N3" component={KN3} />
      <Tab.Screen name="N2" component={KNN2} />
      <Tab.Screen name="N1" component={KN1} />
    </Tab.Navigator>
  )
}