import { View, Image, ScrollView, TouchableOpacity } from 'react-native'
import React from 'react'

export default function Beranda({ navigation }) {
  return (
    <View style={{ flex: 1, backgroundColor: 'white' }}>

      <ScrollView style={{ bottom: 17 }}
        showsVerticalScrollIndicator={false}
        overScrollMode={'never'}>

        <View style={{ height: 175, justifyContent: "center", alignItems: "center" }}>
          <Image source={require('./../../src/Beranda/Banner.png')} style={{ width: 320, height: 140, backgroundColor: 'gray', borderRadius: 20 }} />
        </View>

        <View style={{ height: 425, flexDirection: "row" }}>

          <View style={{ flex: 1, flexDirection: "column" }}>
            <TouchableOpacity onPress={() => navigation.navigate('Hikana')}>
              <Image source={require('./../../src/Beranda/hiragana-katakana.png')} style={{ width: 150, height: 230, backgroundColor: "gray", borderRadius: 20, marginLeft: 20, marginTop: 10 }} />
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Kanji')}>
              <Image source={require('./../../src/Beranda/kanji.png')} style={{ width: 150, height: 150, backgroundColor: "gray", borderRadius: 20, marginTop: 20, marginLeft: 20 }} />
            </TouchableOpacity>
          </View>
          <View style={{ flex: 1, flexDirection: "column" }}>
            <TouchableOpacity onPress={() => navigation.navigate('Grammar')}>
              <Image source={require('./../../src/Beranda/lesson.png')} style={{ width: 150, height: 160, backgroundColor: "gray", borderRadius: 20, marginLeft: 10, marginTop: 10 }} />
            </TouchableOpacity>
            <TouchableOpacity onPress={() => navigation.navigate('Test')}>
              <Image source={require('./../../src/Beranda/test.png')} style={{ width: 150, height: 220, backgroundColor: "gray", borderRadius: 20, marginTop: 20, marginLeft: 10 }} />
            </TouchableOpacity>
          </View>
        </View>

        <View style={{ height: 100, alignItems: "center" }}>
          <Image source={require('./../../src/Beranda/ads.png')} style={{ width: 320, height: 70, borderRadius: 20, backgroundColor: "gray", marginTop: 10 }} />
        </View>
      </ScrollView>
    </View>
  )
}