import { View, Text, FlatList, TouchableOpacity, Image } from 'react-native'
import GestureRecognizer from 'react-native-swipe-gestures';


export default function Grammar({ navigation }) {
  const data = [
    { level: 'N5', tingkat: 'For Beginner', page: 'N5' },
    { level: 'N4', tingkat: 'Intermediate', page: 'ComingSoon' },
    { level: 'N3', tingkat: 'Upper Middle', page: 'ComingSoon' },
    { level: 'N2', tingkat: 'Hard Level', page: 'ComingSoon' },
    { level: 'N1', tingkat: 'Very Difficult', page: 'ComingSoon' }
  ];

  const renderItem = ({ item }) => {
    return (
      <>
        <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center', backgroundColor: "#EEEEF0" }}>
          <TouchableOpacity style={{
            width: 321, height: 79, borderRadius: 20, backgroundColor: 'white', marginTop: 20, flexDirection: 'row', overflow: 'hidden'
          }} onPress={() => navigation.navigate(item.page)}>
            <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
              <View style={{ height: 50, width: 50, borderRadius: 10, backgroundColor: "#7650F5", justifyContent: 'center', alignItems: 'center', marginLeft: 30 }}>
                <Text style={{ color: "white", fontWeight: 'bold', fontSize: 20 }}>{item.level}</Text>
              </View>
            </View>
            <View style={{ flex: 4, justifyContent: "center", alignItems: 'center' }}>
              <Text style={{ fontSize: 17, marginLeft: 20, fontWeight: '500' }}>{item.tingkat}</Text>
            </View>
            <View style={{ flex: 2, alignItems: "flex-end" }}>
              <Image source={require('./../../src/Grammar/C.png')} style={{ width: 65, height: 65 }} />
            </View>
          </TouchableOpacity>
        </View>
      </>
    )
  }

  return (
    <>
       <View>
      <FlatList
        data={data}
        renderItem={renderItem}
        scrollEnabled={false}
      />
      <Image source={require('./../../src/Grammar/admob.png')} style={{
        height: 70, width: 320, marginTop: 20, alignSelf: 'center'
      }} />
    </View>
    </>
  
  )
}