import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import Hiragana from './Hiragana';
import Katakana from './Katakana';

const Tab = createMaterialTopTabNavigator();

export default function Hikana() {

  return (
    <>
      <Tab.Navigator screenOptions={{
        tabBarIndicatorContainerStyle: { backgroundColor: '#F56564', },
        tabBarIndicatorStyle: {
          backgroundColor: "white", width: 100, height: 6,
          borderRadius: 20, marginLeft: 40, elevation: 5
        }, tabBarActiveTintColor: "white",
        tabBarLabelStyle: { fontWeight: "bold", fontSize: 18 }, lazy: true, tabBarStyle: { elevation: 0 },
       }}>
        <Tab.Screen name="Hiragana" component={Hiragana} />
        <Tab.Screen name="Katakana" component={Katakana} />
      </Tab.Navigator>
    </>
  )
}