import { View, Text, ScrollView, TouchableOpacity, Image } from 'react-native'
import React from 'react'

const Kkatakana = (props) => {
    return (
        <View>
            <View style={{ height: 60, alignItems: 'flex-end', marginTop: 20, flexDirection: 'row', marginLeft: 20 }}>
                <View style={{ height: 25, width: 25, borderRadius: 100, backgroundColor: '#F56564' }}></View>
                <Text style={{ marginLeft: 20, fontSize: 20, fontWeight: 'bold' }}>{props.tipe}</Text>
            </View>
            <Text style={{ fontSize: 15, width: props.width, marginTop: 20, marginLeft: 20, textAlign: 'justify' }}>
                {props.deksripsi}
            </Text>
            <Image style={{ marginHorizontal: 20, marginTop: 20, borderRadius: 20, height: 335, backgroundColor: 'gray', marginBottom: 20 }}
            source={props.source}
            />
        </View>
    )
}
export default function Katakana({navigation}) {
    return (
        <View style={{ flex: 1 }} >
            <ScrollView style={{ flex: 1 }} overScrollMode='never'>
                <Text style={{ marginHorizontal: 20, marginTop: 20, fontSize: 15, textAlign: 'justify' }}>
                Katakana dipakai untuk menulis kata-kata yang berasal dari bahasa asing, nama orang asing, nama negara, dan kota luar negeri, telegram 
                dan lain-lainnya. Sering pula dipakai orang untuk menulis nama-nama perusahaan dan kata-kata yang perlu ditonjolkan dalam sebuah kalimat.
                </Text>

                <Kkatakana
                    tipe='SEI-ON'
                    deksripsi='Merupakan huruf Katakana dasar yang berjumlah 46 Huruf.'
                    width={179}
                />

                <Kkatakana
                    tipe='DAKU-ON'
                    deksripsi='Dakuon adalah huruf-huruf Katakana dasar dengan menambahkan (“) tanda tenten (titik dua yang diletakkan sebelah kanan atas huruf Katakana dasar).'
                    width={250}
                />

                <Kkatakana
                    tipe='HANDAKU-ON'
                    deksripsi='Handakuon adalah huruf-huruf Katakana dasar dengan menambahkan (o) tanda maru (lingkaran kecil yang diletakkan sebelah kanan atas huruf Katakana dasar).'
                    width={260}
                />

                <Kkatakana
                    tipe='YOO-ON'
                    deksripsi='Suara gabungan dari suara ki, shi, chi, ni, hi, mi, ri, gi, ji, bi, dengan suara ya, yu dan yo (ya, yu, yo ditulis lebih kecil).'
                    width={260}
                />

            </ScrollView>
            <View style={{
                height: 100, backgroundColor: "white",
                justifyContent: 'center', alignItems: 'center', backgroundColor: '#F56564'
            }}>
                <TouchableOpacity
                onPress={() => navigation.navigate('FlashCardK')}>
                    <Text style={{ fontSize: 18, fontWeight: 'bold', color: 'white' }}>START FALSH CARD</Text>
                </TouchableOpacity>
            </View>
        </View>
    )
}