import { View, Text, FlatList } from 'react-native'
import React from 'react'

const data =[
 {name:'dika',
 name:'diki',
 name:'dika',
 name:'dika',
 name:'dika',}
]

const renderItem = ({item}) => {
  return(
    <View>
     <Text>{item.name}</Text>
    </View>
  )
}
export default function Bab1() {
  return (
    <View>
    <FlatList
      data={data}
      renderItem={renderItem}
    />
    </View>
  )
}