import { View, Text, Image, ScrollView, FlatList, TouchableOpacity } from 'react-native'
import React from 'react'
import { N5data } from './N5data'

export default function N5({navigation}) {

    const renderItem = ({ item }) => {
        return (
            <TouchableOpacity onPress={() => navigation.navigate(item.page)}>
                <View style={{ height: 80, width: 320, borderBottomWidth: 1, borderColor: 'rgba(0, 0, 0, 0.2)', alignSelf: 'center', marginTop: 10 }}>
                    <Text style={{ fontSize: 20, fontWeight: 'bold', color: '#7650F5' }}>
                        {item.bab}
                    </Text>
                    <Text style={{ fontSize: 12, width: 320, top: 10, marginBottom: 10 }}>
                        {item.deskripsi}
                    </Text>
                </View>
            </TouchableOpacity>
        )
    }

    return (
        <View style={{ flex: 1, backgroundColor: 'white' }}>
            <View >
                <View style={{ height: 130, backgroundColor: '#7650F5', flexDirection: 'row', alignItems: "flex-end" }}>
                    <View style={{ height: 66, width: 66, borderRadius: 20, backgroundColor: "white", justifyContent: 'center', alignItems: 'center', marginLeft: 20, marginBottom: 40 }}>
                        <Text style={{ color: "#7650F5", fontWeight: 'bold', fontSize: 25 }}>N5</Text>
                    </View>
                    <View style={{ flexDirection: "column", flex: 1 }}>
                        <Text style={{ fontSize: 20, fontWeight: 'bold', marginLeft: 20, color: 'white' }}>For Beginner</Text>
                        <Text style={{ fontSize: 12, marginLeft: 20, width: 156, marginTop: 10, color: 'white', marginBottom: 40 }}>Untuk pemula yang terdiri dari 25 bab materi</Text>
                    </View>
                </View>
                <View style={{ height: 20, backgroundColor: 'white', borderTopLeftRadius: 40, borderTopRightRadius: 40, zIndex: 2, bottom: 20}}>
                </View>
                <FlatList
                    data={N5data}
                    renderItem={renderItem}
                    overScrollMode={'never'}
                    showsVerticalScrollIndicator={false}
                    style={{ marginBottom: 150 }}
                />
            </View>

        </View>
    )
}