import React from 'react'
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs'
import Reading from './Reading';
import Listening from './Listening';
import Voice from './Voice';

const Tab = createMaterialTopTabNavigator();

export default function Test() {
  return (
    <Tab.Navigator screenOptions={{
      tabBarStyle: { backgroundColor: 'white', elevation: 0, height: 70, justifyContent: 'flex-end', marginBottom: 20 },
      tabBarPressColor: 'white', tabBarIndicatorStyle: {
        backgroundColor: '#90D142',
        height: 40, width: 80, borderRadius: 10, marginLeft: 20, marginBottom: 4
      }, tabBarInactiveTintColor: '#90D142',
      tabBarActiveTintColor: 'white', tabBarLabelStyle: { fontWeight: '600' }
    }} style={{ backgroundColor: 'white' }}>
      <Tab.Screen name="Reading" component={Reading} />
      <Tab.Screen name="Listening" component={Listening} options={{
        tabBarIndicatorStyle: {
          backgroundColor: '#90D142',
          height: 40, width: 90, borderRadius: 10, marginBottom: 4, marginLeft: 15
        }
      }} />
      <Tab.Screen name="Voice" component={Voice} />
    </Tab.Navigator>
  )
}